@import "../../assets/style/app";

.app {
  background: #405F96;
  position: relative;

  .cardWrapper {
    padding-top: 80px;

    @include desktop {
      padding-top: 130px;

      .card {
        overflow: hidden;

        @include desktop {
          width: 1270px;
          margin-left: auto;
          margin-right: auto;
          border: 7px solid #8491AF;
          border-radius: 28px;

        }

        .bottom {
          background-image: url(../../assets/png/card/back.png);
          background-size: cover;
          background-position: center;
        }

      }
    }
  }
}
